    body {
        color: oldlace;
        background-color: rgb(15, 15, 15);
    }
    
    a:link {
        color: blue;
        background-color: #eb6;
    }
    
    a:visited {
        color: rgba(0, 0, 0, 0);
        background-color: rgb(75, 74, 77);
    }
    
    a:hover {
        color: mintcream;
        background-color: #333333;
    }
    
    a:active {
        color: #778;
        background-color: #ec8;
    }
    
    header {
        color: teal;
        background-color: #667;
    }
    
    header nav {
        color: oldlace;
        background-color: black;
    }
    
    article {
        color: #223;
        background-color: #667;
    }
    
    header nav ul {
        border-color: #99a;
    }
    
    header nav a:link,
    header nav a:visited {
        color: #eff;
        background-color: transparent;
        border-color: #99a;
    }
    
    header nav a:hover {
        color: #445;
        background-color: rgba(14, 13, 13, 0);
    }
    
    header nav a:active {
        color: #667;
        background-color: white;
    }
    
    article img {
        color: #ba9;
        background-color: #dcb;
    }
    
    #imagegallery {
        background-color: transparent;
    }
    
    header nav li {
        display: inline;
    }
    
    header nav {
        background-repeat: repeat-x;
        background-position: bottom left;
        border-width: .1em;
        border-style: solid;
        border-bottom-width: 0;
        border-top-width: 0;
        padding-left: 5%;
        padding-right: 5%;
    }
    
    header nav ul {
        box-shadow: 1px 1px 5px 1px;
        background-color: rgba(128, 0, 0, 0.555);
        width: 100%;
        overflow: hidden;
        border-left-width: .1em;
        border-left-style: solid;
        border-right-style: solid;
        border-right-width: .1em;
        padding-left: .5%;
        padding-right: .1%;
        border-top: 2px solid maroon;
    }
    
    header nav li {
        display: inline;
    }
    
    header nav li a {
        display: block;
        float: left;
        padding: 1em 1em 1em;
        line-height: 1.8em;
        align-content: center;
        justify-content: center;
        border-top: 2px solid maroon;
    }
    
    hr {
        width: 600px;
        border: 3px solid rgba(128, 0, 0, 0.623);
    }
    
    h1 {
        border: 10px double rgba(128, 0, 0, 0.555);
        padding: 10px 10px;
    }